<template>
  <div>
    <!-- 顶部 -->
    <van-row>
      <van-col span="20">
        <van-search placeholder="搜索" @focus="getSearch" />
      </van-col>
      <van-col span="4">
        <van-icon
          class="iconS"
          name="https://goods3.juancdn.com/bao/170421/4/9/58f9f3bca43d1f15ff678b8c_132x132.png"
          @click="ify"
        />
      </van-col>
    </van-row>

    <!-- 轮播图 -->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="(item,index) in $store.state.home.homeSwipe" :key="index">
        <img :src="item"  />
      </van-swipe-item>
    </van-swipe>
    <!-- 秒杀宫格 -->
    <van-grid :border="false" :column-num="4" :gutter="0">
      <van-grid-item v-for="(item,index) in $store.state.home.bannerList" :key="index" >
        <van-image :src="item.url" @click="$router.push({path:`/shoppingClass`,query:{ text: item.text}})"/>
      </van-grid-item>
    </van-grid>
    <!-- 优惠卷 -->
    <a href="#" class="activity" @click="$router.push('/coupon')">
      <img src="https://s2.juancdn.com/jas/200307/4/2/5e6319e633b089725b3b71c8_1080x330.gif" alt />
    </a>

    <!-- 活动宫格 -->
    <van-grid :border="false" :column-num="3">
      <van-grid-item v-for="(item,index) in $store.state.home.activityList" :key="index" @click="$router.push({path:'/classify',query:{ ify: index}})">
        <van-image :src="item" />
      </van-grid-item>
    </van-grid>
    <!-- 精选口碑 -->
    <div class="text">
      <img
        src="https://goods7.juancdn.com/jas/180518/c/6/5afe2f0db6f8ea096f2bfb35_1080x138.png?iopcmd=convert&Q=85&dst=png"
        alt
      />
    </div>
      <div class="shopping">
        <!-- 精选专场 -->
        <van-tabs v-model="active" line-width="60" title-active-color="#ee0a24">
          <van-tab title="精选专场">
            <van-row>
              <van-col
                span="12"
                v-for="(item,index) in $store.state.home.selectedSpecial"
                :key="index"
                 @click="$router.push({path:`/shoppingClass`,query:{ text: item.cmTdod}})"
              >
                <div class="box">
                  <img :src="item.url" alt />
                </div>

                <div class="tilXH">{{item.tilXH}}</div>
                <div class="box">
                  <div class="cmTdod">{{item.cmTdod}}</div>
                  <div class="surplus">{{item.surplus}}</div>
                </div>
              </van-col>
            </van-row>
          </van-tab>
          <!-- 精选单品 -->
          <van-tab title="精选单品">
            <van-row>
              <van-col
                span="12"
                v-for="(item,index) in $store.state.home.selectedSingle"
                :key="index"
              >
                <div class="box" @click="$router.push({path:'/details',query:{ index: index}})">
                  <img :src="item.url" alt />
                </div>
                <div class="price">
                  <span class="present-price">{{item.presentPrice}}</span>
                  <span class="original-price">{{item.originalPrice}}</span>
                </div>
                <p class="describe">{{item.describe}}</p>
              </van-col>
            </van-row>
          </van-tab>
        </van-tabs>
      </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: 2
    };
  },
  methods: {
    ify() {
      this.$router.push("/classify");
    },
    getSearch() {
      this.$store.state.bool = false;
      // console.log(this);
      this.$router.push("/search");
    }
  }
};
</script>

<style lang="scss"  >
.van-search {
  padding: 5px;
}
.iconS {
  font-size: 40px;
  text-align: center;
  display: block;
}
.van-swipe-item img {
  width: 100%;
}
.van-grid-item__content {
  padding: 0;
}
.activity img {
  width: 100%;
}
.text {
  margin-top: 0.426667rem;
}
.text img {
  width: 100%;
}
// .van-tabs--line .van-tabs__wrap {
//   width: 50%;
// }
.box {
  padding-right: 1px;
}
.box img {
  width: 100%;
}
.price {
  padding-left: 0.3413333333333333rem;
  padding-right: 0.3413333333333333rem;
  color: #bbb;
  font-size: 0.4693333333333333rem;
  line-height: 0.9386666666666666rem;
  margin-top: 0.256rem;
  display: flex;
  align-items: center;
  position: relative;
}
.present-price {
  font-size: 0.64rem;
  color: #ff464e;
  margin-right: 0.17066666666666666rem;
}
.original-price {
  text-decoration: line-through;
}
.describe {
  position: relative;
  font-size: 0.512rem;
  color: #3b3b3b;
  padding-left: 0.3413333333333333rem;
  padding-right: 2.6453333333333333rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-bottom: 0.512rem;
}
.box{
  position: relative;
}
.tilXH {
  padding: 0px 10px;
  color: #bbb;
  font-size: 16px;
  line-height: 0.9386666666666666rem;
  margin-top: 8px;
  display: flex;
  align-items: center;
  position: relative;
  color: #ff464e;
}
.cmTdod {
  position: relative;
  font-size: 14px;
  color: #3b3b3b;
  padding: 0px 50px 0 10px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-bottom: 0.512rem;
}
.surplus{
  color: #bbb;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 10px;
    font-size: 12px;
}
</style>